:root {
  --primary-bg-color: #822626;
  --left-column-bg-color: #11fc2c;
  --right-column-bg-color: #f4e808;
  --header-footer-bg-color: #333;
  --text-color: #fff;
  --font-family: "Taviraj", serif;
  --main-content-width: 60%;
  --column-width: 20%;
  --responsive-breakpoint: 768px; /* Define a CSS variable for responsive breakpoint */
}
/* Temporarily visualize all elements */
* {
  border: 1px solid red; /* Change 'red' to any color that stands out on your page */
}

h1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-style: italic;
  }
/* Update to body still applies */
body {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column; /* Keeps the Header and Footer in the column */
  min-height: 100%;
}

.app-body {
  display: flex; /* New flex container */
  flex-grow: 1; /* Takes up any available space between header and footer */
}

.main-content {
  background-color: var(--primary-bg-color);
  width: var(--main-content-width);
  margin: 0 auto; /* Centralizes main content within the flex container */
  /* Removes flex-grow and align-items stretch as it's now centrally controlled */
}

.left-column, .right-column {
  flex: 1; /* Ensures side columns fill the available space, consider adjusting based on layout */
  min-height: 100%; /* Maintains full height */
}

.left-column {
  background-color: var(--left-column-bg-color);
}

.right-column {
  background-color: var(--right-column-bg-color);
}

/* Remainder stays the same */


header, footer {
  background-color: var(--header-footer-bg-color);
  color: var(--text-color);
  padding: 10px 0;
  text-align: center;
}

footer {
  align-self: stretch; /* Stretches the footer to match its parent's width */
}

/* Responsive Design Update */
@media (max-width: var(--responsive-breakpoint)) {
  .left-column, .right-column {
    display: none; /* Hides side columns on smaller screens for better readability */
  }
  .main-content {
    width: 100%; /* Allows main content to fill the screen on smaller devices */
  }
}
